import * as axios from 'axios';
import store from '../../store'
import {Message} from 'element-ui'

axios.defaults.withCredentials = true;
export default class Api {

    static domain() {
        return 'http://alliance.fh.81youxi.com/admin/';
        //
        // return '/admin/';
    }

    static authHeaders() {
        if (store.state.user != null) {
            // noinspection JSAnnotator
            return {
                ContentType: 'application/json',
                // 'access-token':
                'access-token': store.state.user.admin_token
            };
        }
    }

    static url() {
        return this.domain();
    }

    static request(url = '', callback = null, errorBack = null, data = {}, params = {}) {
        // let time = new Date().getTime();
        axios.request({
            url: url,
            method: 'POST',
            data: data,
            params: params,
            withCredentials: true,
            headers: this.authHeaders(),
        }).then((response) => {
            console.log(response.data);
            if (response.data.code != 200 && response.data.code != 201) {
                Message.error(response.data.message);
                if (errorBack instanceof Function) {
                    errorBack(response);
                }
                return;
            }
            if (callback instanceof Function) {
                callback(response);
            }
        }).catch((error) => {
            if (errorBack instanceof Function) {
                errorBack(error);
            }
            console.log(error);
        });
    }

    static isStrValid(str) {
        return !(str === undefined || str === null || str.length === 0);
    }

    static objToFormData(obj) {
        let form = new FormData();
        for (let key in obj) {
            if (obj[key] != null) {
                form.append(key, obj[key]);
            }
        }
        return form;
    }

    static deepCopy(obj) {
        if (typeof obj !== 'object') {
            return obj;
        }
        let newobj = {};
        for (var attr in obj) {
            newobj[attr] = this.deepCopy(obj[attr]);
        }
        return newobj;
    }
}
