import Api from './baseApi'

export default class permissionApi extends Api {
    static node( callback, errorBack) {
        super.request(this.uri() + 'node', callback, errorBack)
    }

    static add(data, callback, errorBack) {
        super.request(this.uri() + 'save', callback, errorBack, data)
    }

    static nodeList(data,callback, errorBack) {
        super.request(this.uri() + 'entry', callback, errorBack,data)
    }

    static delete(data, callback, errorBack) {
        super.request(this.uri() + 'delete', callback, errorBack, data)
    }

    static detail(data, callback, errorBack) {
        super.request(this.uri() + 'detail', callback, errorBack, data)
    }

    static uri() {
        return super.url() + 'permission/'
    }

}
