<template>
    <div>
        <!--<quill-editor-->
        <!---->
        <!--ref="myQuillEditor"-->
        <!--v-model="str"-->
        <!--:disabled="disabled"-->
        <!--:options="editorOption"-->
        <!--&gt;-->
        <!--</quill-editor>-->

        <div class="quill-editor-example" id="editorElem"></div>

    </div>
</template>

<script>
    // import {Quill} from 'vue-quill-editor'
    // import {container, ImageExtend, QuillWatch} from './image'
    import E from 'wangeditor'

    // Quill.register('modules/ImageExtend');
    export default {
        name: "richView",
        props: {
            disabled: Boolean,
            value: String,
        },
        data() {
            return {
                str: '',
                editor:null,
            }
        },
        mounted() {
            this.editor = new E('#editorElem');
            this.editor.customConfig.uploadImgServer = '/admin/image/upload';
            this.editor.customConfig.uploadFileName = 'picture';
            this.editor.customConfig.uploadImgMaxLength = 1;
            this.editor.customConfig.uploadImgHooks = {
                // 如果服务器端返回的不是 {errno:0, data: [...]} 这种格式，可使用该配置
                // （但是，服务器端返回的必须是一个 JSON 格式字符串！！！否则会报错）
                customInsert: function (insertImg, result, editor) {
                    // 图片上传并返回结果，自定义插入图片的事件（而不是编辑器自动插入图片！！！）
                    // insertImg 是插入图片的函数，editor 是编辑器对象，result 是服务器端返回的结果
                    console.log(result.data)
                    // 举例：假如上传图片成功后，服务器端返回的是 {url:'....'} 这种格式，即可这样插入图片：
                    var url = 'http://alliance.fh.81youxi.com'+ result.data
                    insertImg(url)
                    // result 必须是一个 JSON 格式字符串！！！否则报错
                }
            };
            this.editor.customConfig.onchange = (html) => {
                this.str = html;
                this.$emit('input', this.str);
            };
            this.editor.create();
        },
        methods: {
            onEditorChange() {//内容改变事件
                this.$emit('input', this.str);
            },
        },
        watch: {
            value(val) {
                // this.str = val;
                this.editor.txt.html(val)
            },
        }
    }
</script>

