<style scoped lang="less">
    .home {
        height: 100%;
        width: 100%;
        background: #f0f0f0;
        .icon {
            font-size: 26px;
            padding-right: 10px;
        }
        .el-menu-vertical-demo:not(.el-menu--collapse) {
            width: 250px;
        }
        .menu {
            height: 100%;
            text-align: left;
            padding-left: 50px;
            overflow: scroll;
            .logo {
                padding: 20px;
                color: #000000;
                text-align: center;
            }
        }
        .menu::-webkit-scrollbar {
            display: none;
        }
        .header {
            position: fixed;
            left: 300px;
            height: 111px;
            background: #ffffff;
            right: 0;
            top: 0;
            z-index: 2;
            transition: left .3s;
            .title {
                width: 100%;
                height: 60px;
                text-align: left;
                .menuSwitch {
                    line-height: 50px;
                    margin-left: 20px;
                    font-size: 20px;
                }
                .message {
                    text-align: right;
                    padding-right: 100px;
                    .icon {
                        line-height: 50px;
                        font-size: 25px;
                    }
                    .el-dropdown-link {
                        height: 50px;
                        font-size: 18px;
                    }
                }
            }
            .line {
                width: 100%;
                height: 1px;
                background: #f0f0f0;
            }
            .router {
                text-align: left;
                .router-title {
                    padding: 20px 20px 0 20px;
                    color: #8a8a8a;
                }
                h2 {
                    margin-left: 20px;
                }
            }

        }
        .content {
            position: fixed;
            left: 300px;
            padding: 20px;
            right: 0;
            bottom: 0;
            top: 111px;
            z-index: 2;
            transition: left .3s;
            .content-router {
                background: #ffffff;
                width: 100%;
                height: 100%;
                overflow: auto;
                text-align: left;
            }
        }
    }
</style>

<template>
    <div class="home">
        <el-menu
                :style="{'padding-left': isCollapse?'0':'0'}"
                :default-active="$route.meta.index"
                class="el-menu-vertical-demo menu"
                @select="select"
                :collapse="isCollapse">
            <div class="logo">
                <img src="../assets/logo.png" width="30px"/>
                <div v-show="!isCollapse"> 创业论坛</div>
            </div>


            <el-submenu :index="item.name" v-for="(item,index) in permissionList" v-bind:key="item.id">
                <template slot="title">
                    <i :class="'icon iconfont '+item.icon "></i>
                    <span slot="title">{{item.name}}</span>
                </template>

                <el-menu-item v-bind:key="child.id" v-for="child in item.child" :index="child.path.split('/')[2]">
                    {{child.name}}
                </el-menu-item>
            </el-submenu>

        </el-menu>
        <div class="header" :style="{left: isCollapse?'65px':'255px'}">
            <div class="title">
                <el-row>
                    <el-col :span="12">
                        <i @click="menuClick" class="icon-menu  iconfont  menuSwitch"></i>
                    </el-col>
                    <el-col class="message" :span="12">
                        <i class="icon iconfont icon-bell"></i>
                        <el-dropdown @command="handleCommand">
                             <span class="el-dropdown-link">{{$store.state.user.realname}}
                                 <i class="el-icon-arrow-down el-icon--right"></i>
                                    </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="login">登出</el-dropdown-item>
                                <el-dropdown-item command="account">个人中心</el-dropdown-item>
                                <el-dropdown-item command="updatePwd">修改密码</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-col>
                </el-row>
            </div>
            <div class="line"></div>
            <div class="router">
                <div class="router-title">
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ name: item.index }" v-for="item in $route.meta.links"
                                            :key="item.name">{{ item.name }}
                        </el-breadcrumb-item>
                        <el-breadcrumb-item>{{$route.meta.title}}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
            </div>
        </div>
        <div class="content" :style="{left: isCollapse?'65px':'250px'}">
            <div class="content-router">
                <keep-alive>
                    <router-view v-if="$route.meta.keepAlive">
                    </router-view>
                </keep-alive>
                <router-view v-if="!$route.meta.keepAlive"></router-view>
            </div>

        </div>
    </div>

</template>


<script>
    import api from '../js/api/permission'

    export default {
        data() {
            return {
                isCollapse: false,
                permissionList: {},
            };
        },
        mounted() {
            if (this.$store.state.user == null) {
                this.handleCommand('login')
            } else {
                this.loadPermission();

            }
        },
        methods: {
            loadPermission() {
                api.node(res => {
                    this.permissionList = res.data.data;
                    if (this.$route.name === 'home') {
                        this.select(this.permissionList[0].child[0].path.split('/')[2])
                    }
                }, () => {
                    this.handleCommand('login')
                })
            },
            select(key) {
                this.$router.push({
                    name: key
                });
            },
            handleCommand(a) {
                this.$router.push({
                    name: a
                });
            },
            menuClick() {
                this.isCollapse = !this.isCollapse;
            }
        }
    }
</script>
