import Vue from 'vue'
import App from './App.vue'
import {router} from './router/index'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';  // 使用 CSS
import VueQuillEditor from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import richView from './components/richView'
import imageSelect from './components/imgSelect'

Vue.use(VueQuillEditor, /* { default global options } */);
require('./main.css');
Vue.use(ElementUI);
Vue.config.productionTip = false;
Vue.component('richView', richView);
Vue.component('imageSelect', imageSelect);

require("babel-polyfill");

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');
